<template>
  <form id="change-username" class='component' v-on:submit.prevent="changeUsername" method="post">
    <h3>Change Username</h3>

    <div class="form-group">
      <label for='username'>New Username:</label>
      <input v-model.trim='username' type='text' name='username'>
    </div>

    <input type='submit' value='Change Username'>

    <div v-if='success' class="success-message">
      {{ success }}
    </div>

    <div v-if='errors.length' class="error-message">
      <b>Please correct the following error(s):</b>
      <ul>
        <li v-for='error in errors' v-bind:key='error.id'>{{ error }}</li>
      </ul>
    </div>
  </form>
</template>

<script>
import axios from "axios";
import { eventBus } from "../main";

export default {
  name: "ChangeUsername",

  data() {
    return {
      errors: [],
      username: "",
      success: ""
    }
  },

  props: {
    user: Object
  },

  methods: {
    changeUsername: function() {
      this.errors = [];

      if (this.username === "") {
        if (this.username === "") {
          this.errors.push("New username is required");
        }
      } else {
        const bodyContent = {
          username: this.username
        };
        axios
          .put(`/api/users/${this.user.id}`, bodyContent)
          .then(res => {
            this.success = "Username successfully changed"
            eventBus.$emit('change-username-success', true);
          })
          .catch(err => {
            this.errors.push(err.response.data.error);
          })
          .then(() => {
            this.resetForm();
            this.clearMessages();
          });
      }
    },

    resetForm: function() {
      this.username = "";
    },

    clearMessages: function() {
      setInterval(() => {
        this.errors = [];
        this.success = "";
      }, 5000);
    }
  }
}
</script>

<style scoped>
.form-group {
  width: 100%;
  display: flex;
  justify-content: space-between;
}

label {
  margin-right: 0.5rem;
}

form {
  width: fit-content;
}

input[type="text"],
input[type="url"] {
  width: 15rem;
}
</style>
