<template>
  <div class="component">

    <div v-if='success' class="success-message">
      {{ success }}
    </div>

    <div v-if='errors.length' class="error-message">
      <b>Please correct the following error(s):</b>
      <ul>
        <li v-for='error in errors' v-bind:key='error.id'>{{ error }}</li>
      </ul>
    </div>

    <form id='create-freet' v-on:submit.prevent='follow' method='post'>
      <h3>Follow a User</h3>

      <div class='form-group'>
        <input v-model.trim='followUsername' type='text' placeholder="username">
      </div>

      <input type='submit' v-bind:disabled="followUsername === ''">
    </form>

    <form id='create-freet' v-on:submit.prevent='unfollow' method='post'>
      <h3>Unfollow a User</h3>

      <div class='form-group'>
        <input v-model.trim='unfollowUsername' type='text' placeholder="username">
      </div>

      <input type='submit' v-bind:disabled="unfollowUsername === ''">
    </form>
  </div>
</template>

<script>
import axios from "axios";
import { eventBus } from "../main";

export default {
  name: "Follow",

  props: {
    user: Object
  },

  data() {
    return {
      errors: [],
      success: "",
      unfollowUsername: "",
      followUsername: ""
    };
  },

  methods: {
    follow: function() {
      this.errors = [];
      if (this.followUsername === "") {
        this.errors.push("Username cannot be blank");
      } else {
        const body = { followeeUsername: this.followUsername };
        axios
          .post(`/api/users/${this.user.id}/followByUsername`, body)
          .then((res) => {
            this.success = `Successfully followed ${this.followUsername}`;
            eventBus.$emit("follow-success", res);
          })
          .catch(err => {
            this.errors.push(err.response.data.error);
          })
          .then(() => {
            this.followUsername = "";
            this.clearMessages();
          });
      }
    },

    unfollow: function() {
      this.errors = [];
      if (this.unfollowUsername === "") {
        this.errors.push("Username cannot be blank");
      } else {
        axios
          .delete(`/api/users/${this.user.id}/unfollowByUsername/${this.unfollowUsername}`)
          .then((res) => {
            this.success = `Successfully unfollowed ${this.unfollowUsername}`;
            eventBus.$emit("unfollow-success", res);
          })
          .catch(err => {
            this.errors.push(err.response.data.error);
          })
          .then(() => {
            this.unfollowUsername = "";
            this.clearMessages();
          });
      }
    },

    resetForm: function() {
      this.content = "";
    },

    clearMessages: function() {
      setInterval(() => {
        this.errors = [];
        this.success = "";
      }, 5000);
    }
  }
};
</script>

<style scoped>
.form-group {
  width: 100%;
  display: flex;
  justify-content: space-between;
}

label {
  margin-right: 0.5rem;
}

form {
  width: fit-content;
}

input[type="text"],
input[type="url"] {
  width: 15rem;
}

form > * {
  margin-bottom: 1rem;
}
</style>
