<template>
  <form id="change-password" class='component' v-on:submit.prevent="changePassword" method="post">
    <h3>Change Password</h3>

    <div class="form-group">
      <label for='password'>New Password:</label>
      <input v-model.trim='password' type='text' name='password'>
    </div>

    <input type='submit' value='Change Password'>

    <div v-if='success' class="success-message">
      {{ success }}
    </div>

    <div v-if='errors.length' class="error-message">
      <b>Please correct the following error(s):</b>
      <ul>
        <li v-for='error in errors' v-bind:key='error.id'>{{ error }}</li>
      </ul>
    </div>
  </form>
</template>

<script>
import axios from "axios";
import { eventBus } from "../main";

export default {
  name: "ChangePassword",

  data() {
    return {
      errors: [],
      password: "",
      success: ""
    }
  },

  props: {
    user: Object
  },

  methods: {
    changePassword: function() {
      this.errors = [];

      if (this.password === "") {
        if (this.password === "") {
          this.errors.push("New password is required");
        }
      } else {
        const bodyContent = {
          password: this.password
        };
        axios
          .put(`/api/users/${this.user.id}`, bodyContent)
          .then(res => {
            this.success = "Password successfully changed"
            eventBus.$emit('change-password-success', true);
          })
          .catch(err => {
            this.errors.push(err.response.data.error);
          })
          .then(() => {
            this.resetForm();
            this.clearMessages();
          });
      }
    },

    resetForm: function() {
      this.password = "";
    },

    clearMessages: function() {
      setInterval(() => {
        this.errors = [];
        this.success = "";
      }, 5000);
    }
  }
}
</script>

<style scoped>
.form-group {
  width: 100%;
  display: flex;
  justify-content: space-between;
}

label {
  margin-right: 0.5rem;
}

form {
  width: fit-content;
}

input[type="text"],
input[type="url"] {
  width: 15rem;
}
</style>
