<template>
  <div class="component">
    <div v-if='errors.length' class="error-message">
      <b>Please correct the following error(s):</b>
      <ul>
        <li v-for='error in errors' v-bind:key='error.id'>{{ error }}</li>
      </ul>
    </div>

    <span>{{ displayedUser.username }}</span>
    <button
      class="follow"
      v-on:click="follow"
      v-if='!isFollowing && !(displayedUser.id === signedInUser.id)'
    >
      Follow
    </button>

    <button
      class="unfollow"
      v-on:click="unfollow"
      v-if='isFollowing && !(displayedUser.id === signedInUser.id)'
    >
      Unfollow
    </button>

  </div>
</template>

<script>
import axios from "axios";
import { eventBus } from "../main";

export default {
  name: "Profile",

  props: {
    displayedUser: Object,
    signedInUser: Object
  },

  data() {
    return {
      errors: [],
      isFollowing: false
    };
  },

  created: function() {
    this.loadIsFollowing();
  },

  mounted: function() {
    this.loadIsFollowing();
  },

  methods: {
    follow: function() {
      const body = { followeeId: this.displayedUser.id };
      axios
        .post(`/api/users/${this.signedInUser.id}/follow`, body)
        .then((res) => {
          eventBus.$emit("follow-success", res);
        })
        .catch(err => {
          this.errors.push(err.response.data.error);
        })
        .then(() => {
          this.loadIsFollowing();
          this.clearMessages();
        });
    },

    unfollow: function() {
      axios
        .delete(`/api/users/${this.signedInUser.id}/unfollow/${this.displayedUser.id}`)
        .then((res) => {
          eventBus.$emit("unfollow-success", res);
        })
        .catch(err => {
          this.errors.push(err.response.data.error);
        })
        .then(() => {
          this.loadIsFollowing();
          this.clearMessages();
        });
    },

    loadIsFollowing: function() {
      axios
        .get(`/api/users/${this.signedInUser.id}/isFollowing/${this.displayedUser.id}`)
        .then((res) => {
          this.isFollowing = res.data.isFollowing;
        })
        .catch(err => {
          eventBus.$emit("get-isFollowing-error", err);
        })
    },

    clearMessages: function() {
      setInterval(() => {
        this.errors = [];
      }, 5000);
    }
  }
};
</script>

<style scoped>
button {
  border-radius: 5px;
  border-width: 1px;
}
</style>
