<template>
  <div id="sign-out" class="component">
    <button v-on:click="signOut">Sign Out</button>
  </div>
</template>

<script>
import axios from "axios";
import { eventBus } from "../main";

export default {
  name: "SignOut",

  methods: {
    signOut: function() {
      axios.post('api/users/signout')
        .then(() => {
          eventBus.$emit('signout-success', true);
        })
    }
  }
}
</script>
