<template>
  <div id="user-settings" class="component">
    <div v-if='message' class="success-message">
      {{ message }}
    </div>
    <div v-if="isSignedIn">
      <ChangeUsername v-bind:user="user"/>
      <ChangePassword v-bind:user="user"/>
      <DeleteAccount v-bind:user="user"/>
    </div>
    <div v-else>
      <SignIn/>
      <CreateAccount/>
    </div>
  </div>
</template>

<script>
import SignIn from "./SignIn.vue";
import SignOut from "./SignOut.vue";
import CreateAccount from "./CreateAccount.vue";
import ChangeUsername from "./ChangeUsername.vue";
import ChangePassword from "./ChangePassword.vue";
import DeleteAccount from "./DeleteAccount.vue";
import { eventBus } from "../main";

export default {
  name: "UserSettings",

  components: {
    SignIn,
    SignOut,
    CreateAccount,
    ChangeUsername,
    ChangePassword,
    DeleteAccount
  },

  data() {
    return {
      message: ""
    };
  },

  props: {
    isSignedIn: Boolean,
    user: Object
  },

  created: function() {
    eventBus.$on("delete-account-success", () => {
      this.message = "Successfully deleted account.";
      this.clearMessage();
    });
  },

  methods: {
    clearMessage: function() {
      setInterval(() => {
        this.message = "";
      }, 5000);
    }
  }
};
</script>

<style scoped>
#user-settings {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 2rem;
}

#user-settings > * {
  margin: 0.5rem;
}
</style>
