<template>
  <div class="component">
    <form id='create-freet' v-on:submit.prevent='createFreet' method='post'>
      <h3>Compose new Freet</h3>

      <div class='form-group'>
        <input id='content' v-model.trim='content' type='text' name='content' placeholder="What's up?">
      </div>

      <input type='submit' value='Freet' v-bind:disabled="content === ''">

      <div v-if='success' class="success-message">
        {{ success }}
      </div>

      <div v-if='errors.length' class="error-message">
        <b>Please correct the following error(s):</b>
        <ul>
          <li v-for='error in errors' v-bind:key='error.id'>{{ error }}</li>
        </ul>
      </div>
    </form>
  </div>
</template>

<script>
import axios from "axios";
import { eventBus } from "../main";

export default {
  name: "CreateFreet",

  data() {
    return {
      errors: [],
      success: "",
      content: ""
    };
  },

  methods: {
    createFreet: function() {
      this.errors = [];

      if (this.content === "") {
        this.errors.push("Freet cannot be blank");
      } else {
        const bodyContent = { content: this.content };
        axios
          .post("/api/freets", bodyContent)
          .then(freet => {
            this.success = "Freet created successfully!";
            eventBus.$emit("create-freet-success", freet);
          })
          .catch(err => {
            this.errors.push(err.response.data.error);
          })
          .then(() => {
            this.resetForm();
            this.clearMessages();
          });
      }
    },

    resetForm: function() {
      this.content = "";
    },

    clearMessages: function() {
      setInterval(() => {
        this.errors = [];
        this.success = "";
      }, 5000);
    }
  }
};
</script>

<style scoped>
.form-group {
  width: 100%;
  display: flex;
  justify-content: space-between;
}

label {
  margin-right: 0.5rem;
}

form {
  width: fit-content;
}

input[type="text"],
input[type="url"] {
  width: 15rem;
}

form > * {
  margin-bottom: 1rem;
}
</style>
