<template>
  <div id="delete-account" class="component">
    <button class="delete" v-on:click="deleteAccount">Delete Account</button>
  </div>

  <div v-if='errors.length' class="error-message">
    <ul>
      <li v-for='error in errors' v-bind:key='error.id'>{{ error }}</li>
    </ul>
  </div>
</template>

<script>
import axios from "axios";
import { eventBus } from "../main";

export default {
  name: "DeleteAccount",

  data() {
    return {
      errors: []
    };
  },

  props: {
    user: Object
  },

  methods: {
    deleteAccount: function() {
      axios.delete(`api/users/${this.user.id}`)
        .then(() => {
          this.success = "Successfully deleted account";
          eventBus.$emit('delete-account-success', true);
        })
        .catch((err) => {
          this.errors.push(err.response.data.error);
        })
        .then(() => {
          this.clearMessages();
        });
    },

    clearMessages: function() {
      setInterval(() => {
        this.errors = [];
      }, 5000);
    }
  }
}
</script>

<style>
.delete {
  background-color: #f44336;
  color: white;
  border-radius: 5px;
  height: 25px;
}
</style>
