<template>
  <div class="component">
    <p>{{ freet.content }}</p>

    <p>
      <span v-if="!freet.originalFreetId">Author: </span>
      <span v-if="freet.originalFreetId">Original Poster: {{ originalPoster.username }}
        <br/>
        Refreeter:
      </span>
      <span v-if="!user">{{ author.username }}</span>
      <Profile
        v-if="author && user"
        v-bind:signedInUser="user"
        v-bind:displayedUser="author"
      >
    </p>

    <span v-if='user'>
      <button v-on:click="upvoteFreet">
        <img src="./upvote.png" alt="Upvotes" height="15" width="15">
      </button>
    </span>
    <span> {{ votes }} Votes </span>
    <span v-if='user'>
      <button v-on:click="downvoteFreet">
        <img src="./downvote.png" alt="Downvotes" height="15" width="15">
      </button>
    </span>

    <br/>
    <button
      v-on:click="toggleEditMode"
      v-if='user && !isEditMode && user.id === freet.authorId'
    >
      Edit
    </button>

    <button
      v-on:click="refreet"
      v-if='user && user.id !== freet.authorId'
    >
      Refreet
    </button>

    <input
      class="edit"
      v-model.trim='content'
      type="textarea"
      name="content"
      placeholder="Change Freet content"
      v-if='isEditMode'
    >

    <button
      v-on:click="updateFreet"
      v-if='isEditMode'
    >
      Update
    </button>

    <div v-if='errors.length' class="error-message">
      <b>Please correct the following error(s):</b>
      <ul>
        <li v-for='error in errors' v-bind:key='error.id'>{{ error }}</li>
      </ul>
    </div>

    <br/>
    <button
      class="delete"
      v-on:click="deleteFreet"
      v-if='isEditMode'
    >
      Delete
    </button>

    <br/>
    <button
      v-on:click="toggleEditMode"
      v-if='isEditMode'
    >
      Cancel
    </button>
  </div>
</template>

<script>
import axios from "axios";
import { eventBus } from "../main";
import Profile from "./Profile";

export default {
  name: "FreetItem",
  components: { Profile },

  props: {
    freet: Object,
    user: Object
  },

  data() {
    return {
      author: "",
      content: "",
      errors: [],
      isEditMode: false,
      votes: 0,
      originalPoster: ""
    };
  },

  created: function() {
    this.loadAuthor();
    this.loadVotes();
  },

  mounted: function() {
    this.loadAuthor();
    this.loadVotes();
  },

  methods: {
    updateFreet: function() {
      const body = { content: this.content };
      axios
        .put(`/api/freets/${this.freet.id}`, body)
        .then((res) => {
          eventBus.$emit("update-freet-success", res);
        })
        .catch(err => {
          this.errors.push(err.response.data.error);
        })
        .then(() => {
          this.content = "";
          this.clearMessages();
        });
    },

    refreet: function() {
      const body = { freetId: this.freet.id, userId: this.user.id };
      axios
        .post(`/api/freets/refreet`, body)
        .then(() => {
          eventBus.$emit("create-freet-success");
        })
        .catch(err => {
          this.errors.push(err.response.data.error);
          eventBus.$emit("refreet-error");
        })
    },

    deleteFreet: function() {
      axios
        .delete(`/api/freets/${this.freet.id}`, {})
        .then(() => {
          eventBus.$emit("delete-freet-success", this.freet);
        })
        .catch(err => {
          eventBus.$emit("delete-freet-error", err);
        })
    },

    upvoteFreet: function() {
      axios
        .put(`/api/freets/${this.freet.id}/upvote`)
        .then(() => {
          eventBus.$emit("vote-freet-success", this.freet);
          this.loadVotes();
        })
        .catch(err => {
          eventBus.$emit("upvote-freet-error", err);
        })
    },

    downvoteFreet: function() {
      axios
        .put(`/api/freets/${this.freet.id}/downvote`)
        .then(() => {
          eventBus.$emit("vote-freet-success", this.freet);
          this.loadVotes();
        })
        .catch(err => {
          eventBus.$emit("downvote-freet-error", err);
        })
    },

    loadAuthor: function() {
      axios
        .get(`/api/users/${this.freet.authorId}`)
        .then((res) => {
          this.author = res.data;
        })
        .catch(err => {
          eventBus.$emit("get-author-error", err);
        });
      if (this.freet.originalFreetId) {
        axios
          .get(`/api/freets/${this.freet.originalFreetId}/author`)
          .then((res) => {
            this.originalPoster = res.data;
          })
          .catch(err => {
            eventBus.$emit("get-author-error", err);
          });
      }
    },

    loadVotes: function() {
      axios
        .get(`/api/freets/${this.freet.id}/votes`)
        .then((res) => {
          this.votes = res.data.votes;
        })
        .catch(err => {
          eventBus.$emit("get-votes-error", err);
        })
    },

    toggleEditMode: function() {
      this.isEditMode = !this.isEditMode;
    },

    clearMessages: function() {
      setInterval(() => {
        this.errors = [];
      }, 5000);
    }
  }
};
</script>

<style scoped>
button {
  border-radius: 5px;
  border-width: 1px;
}
</style>
