<template>
  <form id='create-account' class='component' v-on:submit.prevent='createAccount' method='post'>
    <h3>Create Account</h3>

    <div class="form-group">
      <label for='username'>Username:</label>
      <input v-model.trim='username' type='text' name='username'>
    </div>

    <div class="form-group">
      <label for='password'>Password:</label>
      <input v-model.trim='password' type='password' name='password'>
    </div>

    <input type='submit' value='Create Account'>

    <div v-if='success' class="success-message">
      {{ success }}
    </div>

    <div v-if='errors.length' class="error-message">
      <b>Please correct the following error(s):</b>
      <ul>
        <li v-for='error in errors' v-bind:key='error.id'>{{ error }}</li>
      </ul>
    </div>
  </form>
</template>

<script>
import axios from "axios";
import { eventBus } from "../main";

export default {
  name: "CreateAccount",

  data() {
    return {
      errors: [],
      success: "",
      username: "",
      password: ""
    };
  },

  methods: {
    createAccount: function() {
      this.errors = [];

      if (this.username === "" || this.password === "") {
        if (this.username === "") {
          this.errors.push("Username is required");
        }
        if (this.password === "") {
          this.errors.push("Password is required");
        }
      } else {
        const bodyContent = {
          username: this.username,
          password: this.password
        };
        axios
          .post("/api/users", bodyContent)
          .then(user => {
            this.success = "Successfully created account!";
            eventBus.$emit("create-account-success", user);
          })
          .catch(err => {
            this.errors.push(err.response.data.error);
          })
          .then(() => {
            this.resetForm();
            this.clearMessages();
          });
      }
    },

    resetForm: function() {
      this.username = "";
      this.password = "";
    },

    clearMessages: function() {
      setInterval(() => {
        this.errors = [];
        this.success = "";
      }, 5000);
    }
  }
};
</script>

<style scoped>
.form-group {
  width: 100%;
  display: flex;
  justify-content: space-between;
}

label {
  margin-right: 0.5rem;
}

form {
  width: fit-content;
}

input[type="text"],
input[type="url"] {
  width: 15rem;
}

form > * {
  margin-bottom: 1rem;
}
</style>
