<template>
  <div id="nav-bar">
    <ul>
      <li><a class="active" href="#home" v-on:click="setState('allFreets')">All Freets</a></li>
      <li v-if="isSignedIn"><a href="#" v-on:click="setState('freetFeed')">Freetfeed</a></li>
      <li v-if="isSignedIn"><a href="#" v-on:click="setState('freet')">Freet</a></li>
      <li v-if="isSignedIn"><a href="#" v-on:click="setState('follow')">Follow</a></li>
      <li v-if="isSignedIn"><a href="#" v-on:click="setState('account')">Account</a></li>
      <li v-if="isSignedIn"><a href="#" v-on:click="signOut">Sign Out</a></li>
      <li v-if="isSignedIn === false"><a href="#" v-on:click="setState('account')">Sign In</a></li>
    </ul>
  </div>
</template>

<script>
import axios from "axios";
import { eventBus } from "../main";

export default {
  name: "NavBar",

  data() {
    return {
    };
  },

  props: {
    isSignedIn: Boolean
  },

  methods: {
    setState: function(state) {
      eventBus.$emit('set-state', state);
    },

    signOut: function() {
      axios.post('api/users/signout')
        .then(() => {
          eventBus.$emit('signout-success', true);
          this.setState('account');
        })
    }
  }
};
</script>

<style scoped>

ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
  background-color: #333;
}

li {
  float: left;
}

li a {
  display: block;
  color: white;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
}

li a:hover {
  background-color: #111;
}
</style>
