<template>
  <div class="freets">
    <h3 v-if='type === "allFreets"'>All Freets</h3>
    <h3 v-if='type === "freetFeed"'>Freets of Users You're Following</h3>
    <div v-if='success' class="success-message">
      {{ success }}
    </div>
    <div v-if='error' class="error-message">
      {{ error }}
    </div>

    <div>
      <label for="author">Search Freets By Author:</label>
      <input id='author' v-model.trim='author' type='text' name='author'>
      <button
        v-on:click="viewByAuthor"
      >
        Search
      </button>
    </div>

    Sort By:
    <select v-model="sortBy" v-on:change="onChangeSortBy">
      <option value="mostRecent">Most Recent</option>
      <option value="mostVotes">Most Votes</option>
    </select>

    <div class="freet-list">
      <div v-if="freets.length">
        <FreetItem
          v-for="freet in freets"
          v-bind:key="freet.id"
          v-bind:freet="freet"
          v-bind:user="user"
        />
      </div>
      <div v-else>
        <p>There are no freets to display. Create one today!</p>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import FreetItem from "./FreetItem";

import { eventBus } from "../main";

export default {
  name: "FreetList",

  props: {
    user: Object,
    type: ""
  },

  components: { FreetItem },

  data() {
    return {
      error: "",
      sortBy: "mostRecent",
      success: "",
      freets: []
    };
  },

  created: function() {
    eventBus.$on("create-freet-success", () => {
      this.success = `Freet successfully created`;
      this.clearMessages();
      this.loadFreets(this.type);
    });

    eventBus.$on("update-freet-success", () => {
      this.success = `Freet successfully updated`;
      this.clearMessages();
      this.loadFreets(this.type);
    });

    eventBus.$on("vote-freet-success", () => {
      this.clearMessages();
      this.loadFreets(this.type);
    });

    eventBus.$on("delete-freet-success", () => {
      this.success = `Freet has been deleted`;
      this.clearMessages();
      this.loadFreets(this.type);
    });

    eventBus.$on("update-short-error", res => {
      this.error = `Error updating freet ${res.data.id}`;
      this.clearMessages();
      this.loadFreets(this.type);
    });

    eventBus.$on("delete-short-error", res => {
      this.error = `Error deleting freet ${res.id}`;
      this.clearMessages();
      this.loadFreets(this.type);
    });

    eventBus.$on("set-state", (state) => {
      if (state === 'freetFeed' || state === 'allFreets') { this.loadFreets(state); }
    });
  },

  mounted: function() {
    this.loadFreets(this.type);
  },

  methods: {
    loadFreets: function(type) {
      if (type === 'freetFeed') {
        if (this.user) {
          axios.get(`/api/freets/${this.user.id}/following`).then(response => {
            this.freets = response.data.slice();
          });
        }
      } else { // all freets
        axios.get("/api/freets").then(response => {
          // slice() to copy array
          this.freets = response.data.slice();
        });
      }
    },

    viewByAuthor: function() {
      axios.get(`/api/freets/${this.author}`).then(response => {
        this.freets = response.data;
      });
    },

    clearMessages: function() {
      setInterval(() => {
        this.success = "";
        this.error = "";
      }, 5000);
    },

    onChangeSortBy: function() {
    }
  }
};
</script>

<style scoped>
.freets {
  margin: 0.5rem;
}

.freet-list {
  display: flex;
  flex-wrap: wrap;
}

.freet-list > * {
  margin: 0.5rem;
}
</style>
